//import axios from 'axios';

export function categoriesFetching(isFetching){
    return { 
        type: 'CATEGORIES_FETCHING',
        isFetching 
    };
}

export function recieveCategories (categories){
    return {
        type: 'RECIEVE_CATEGORIES',
        categories,
        isFetching: false
    };
}

export function getCategories() {
    return (dispatch) => {
        dispatch(categoriesFetching(true));
        fetch('http://localhost:3001/categories')
            .then((response) => {
                if (!response.ok) {
                    throw Error(response.statusText);
                }
                // dispatch(categoriesFetching(false));
                return response;
            })
            .then((response) => response.json())
            .then((items) => dispatch(recieveCategories(items)));
    };
}
