const initialState = {
    categoryList: '',
    payload: [],
    isFetching: null
};

const categories = (state = initialState , action) => {
    switch (action.type) {
    
    case 'CATEGORIES_FETCHING':
        return Object.assign({}, state, {
            isFetching: action.isFetching,
        });
    
    case 'RECIEVE_CATEGORIES':
        return Object.assign({}, state, {
            isFetching: action.isFetching,
            categoryList: action.categories
        });

    default:
        return state;
    }
};

export default categories;