import React from 'react';

export default class Index extends React.Component {
    constructor(props){
      super(props);
    }

    renderCategories(){
      const { isFetching, categoryList } = this.props.categories;
      
      switch(isFetching){
        case null:
          return <div> No data </div>

        case true:
          return <div> Loading ... </div>

        case false:
          return <div> <ul> {categoryList.map((category, index)=> <li key={`${index}${name}`}> { category.name } </li> )}  </ul> </div>
      }

    }

    selectCategory = () => {
      this.props.getCategories();
    }
    
    render() {
      console.log('isFetching? ', this.props.categories.isFetching);
      return (
        <div> Hola:
          <button onClick={this.selectCategory}> Click Me</button>
          {this.renderCategories()}
        </div>
      );
    }
}